.App {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 800px;
  padding-left: 10px;
  margin: 50px auto;
  background-color: #a8ffb7;
  border-radius: 10px;
  box-shadow: 0 14px 45px rgba(0, 0, 0, 0.22), 0 18px 45px rgba(0, 0, 0, 0.22);
  transform: scale(1.0);
}